body {
    padding: 0;
    margin: 0 0;
    font-family: sans-serif;
  }
.navbar li {
    display: inline-block;
    text-align: center;
    font-family: 'Permanent Marker', cursive;
}

.itemLeft {
    width: 15%;
}

.itemCenter {
  width: 70%;
}

.itemRight {
    width: 15%;
}

.navbar {
    background: black;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

li {
    color: white;
    padding: 15px 0;
}

body {
    font-size: 24px;
}

.fab, .far{
    color: white;   
}

.header{
	width: 100%;
	position: relative;
	background-image: url(/react-memory-game/static/media/baseballbackground.25c883a9.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 20% 50%;	
	font-family: 'Cinzel', serif;
	
}

h1{
	text-align: center;
	font-size: 90px;
	font-weight: bold;
	padding: 90px 0 20px 0; 
	color: white;
	text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
	font-family: 'Permanent Marker', cursive;
}

h2{
	text-align: center;
	font-size: 25px;
	padding-bottom: 40px;
	color: white;
	text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
	font-family: 'Permanent Marker', cursive;
}

.card {
    width: 200px;
    height: 280px;
    margin: 15px 45px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    border: 3px solid white;
  }

img {
    height: 100%;
    width: 100%
}
.wrapper {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 25px 0 50px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
  }
